.LessonCard.card {
    background-color: var(--grandmas-hair)!important;
}

.LessonCard .btn-primary {
    color: #fff;
    background-color: var(--literally-just-your-shirt);
    border-color: var(--literally-just-your-shirt);
    border-radius: 0.4rem;
}

.LessonCard .btn-primary:hover {
    color: #fff;
    filter: brightness(85%);
    background-color: var(--literally-just-your-shirt);
    border-color: var(--literally-just-your-shirt);
}

.TeacherHomepage h1 {
    font-family: "Nunito", sans-serif;
    font-size: 2.7rem;
    color: var(--just-blue);
    font-weight: 650;
    margin-block-start: 2rem;
    margin-block-end: 1rem;
}

.TeacherHomepage .card-title {
    font-family: "Source Sans Pro", sans-serif;
    font-size: 1.4rem;
    font-weight: 700;
    color: var(--pink-i-think);
}

.TeacherHomepage .card-text {
    font-family: Georgia, serif;
}

.TeacherHomepage-card-progress {
    background-color: rebeccapurple;
    height: 0.25rem;
    clip: auto;
}
